import React, { useEffect, useState } from 'react'
import './navAdd.css';
import shoppingCartSolid from '../images/shoppingCartSolid.png'
import ledaerSmall from '../images/ledaerSmall.png';
import leaderNew from '../images/leaderNew.png'

export default () => {

    const [onceTime, setOnceTime] = useState(true)
    const [userName, setUserName] = useState("");

    useEffect(() => {
        if (onceTime) {
            // debugger;
            let userNameTemp=window.location.pathname.split('/')[1];
            setUserName(userNameTemp);

            setOnceTime(false);
        }
    })


    return (
        <nav class="navbar navbar-expand-lg navbar-light " style={{backgroundColor:'white'}}>
            {/* <a class="navbar-brand" href="#">Navbar</a> */}
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse navnav" id="navbarNavAltMarkup">
                <div class="navbar-nav">
                    <div style={{ width: '12vw' }}></div>
                    <div class="nav-item nav-link active">
                        <img src={leaderNew} />
                    </div>
                    {/* <div class="leader"> */}
                    <h5 className="nav-item nav-link leaderLogo mt-2" style={{ color: '#4E4EFE' }}>LEADER</h5>
                    {/* </div> */}
                   
                    <div style={{ width: '25vw' }}>
                        
                    </div>
                    {/* <a class="nav-item nav-link disabled" href="#" style={{color:'black',fontSize:'large'}}>Hellow {userName}</a> */}
                    {/*  */}
                </div>
            </div>
        </nav>
    )
}