import { actions } from '../actions/staticAction';

export const getUserName = ({ dispatch, getState }) => next => action => {
        debugger;
        var url = window.location
        var userName = (url.pathname.split)('/')[1]
        if (action.type === 'GET_USER_NAME') {
                return fetch("https://lobby.leader.codes/api/getUser/"
                        + userName
                ).then((res) => res.json())
                        .then((resJson) => {
                                dispatch({ type: "INIT_DATA", payload: resJson });
                        })
                        .catch((err) => {
                                console.log(err)
                        })
        }
        return next(action);
};
export const getStaticData = ({ dispatch, getState }) => next => action => {
        if (action.type === 'INIT_DATA') {
                return fetch('https://lobby.leader.codes/api/getAllData/' + action.payload.uid).then((res) => res.json())
                        .then((resJson) => {
                                dispatch(actions.setStaticData(resJson));
                        })
                        .catch((err) => {
                                console.log(err)
                        })
        }
        return next(action);
};

