import { createStore, combineReducers, applyMiddleware } from 'redux';
import {getUserName, getStaticData} from '../middleWares/crudMiddleware'
import staticDetails from '../reducers/staticDeatails'

const state = {
         
};

const reducer = combineReducers({staticDetails});

const store = createStore(reducer, applyMiddleware(getStaticData,getUserName));
window.store = store;
export default store;
store.dispatch({ type: 'GET_USER_NAME' });